@import 'variables.scss';

.page-header {
  position: relative;
  background-color: rgba(0, 0, 0, 0.3);
  min-height: 462px;

  &__wrapper {
    width: $mobile-size-width;
    margin: 0 auto;
    display: flex;
    position: relative;
    padding: 19px 0 18px 0;
  }

  &::before {
    content: "";
    position: absolute;
    width: 320px;
    height: 462px;
    background-image: url(../img/bg.jpg);
    background-repeat: no-repeat;
    z-index: -1;
  }

  &__slogan {
    margin: 0;
    text-align: center;
    color: $basic-white;
    font-weight: 300;
    font-size: 24px;
  }

  &__wrapper-slogan {
    width: $mobile-size-width;
    margin: 0 auto;
    padding-top: 29px;
  }
}

